@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
@import url(/src/reset.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #737373;
}
.container {
  max-width: 1280px;
  margin: 0 auto;
}
@media only screen and (max-width: 768px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
  }
}

.hero{
    background-image: linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/Vadim74-web/air-quality/static/media/Hero-BG.3573d221.jpg);
    background-size: cover;
    padding: 80px 0 178px;
}
.hero-partners {
        display: flex;
        align-items: center;
}
.hero-partners-item {
            margin-left: 60px;
}
.hero-partners-item:first-child {
                margin-left: 0;
}       
.hero-burger, .hero-burger-show {
    display: none;
}
.hero-navigation {
        margin-top: 45px;
}
.hero-navigation-item {
            text-decoration: none;
            font-weight: normal;
            font-size: 19px;
            line-height: 22px;
            color: #ffffff;
            margin-left: 75px;
}
.hero-navigation-item:first-child {
                margin-left: 0;
}

 

.hero-title {
        width: 665px;
        height: 130px;
        font-weight: normal;
        font-size: 57px;
        line-height: 67px;
        color: #ffffff;
        margin: 108px 0 132px;
}

.hero-link {
        display: inline-block;
        text-decoration: none;
        padding: 35px 151px;
        font-weight: normal;
        font-size: 32px;
        line-height: 37px;
        color: #ffffff;
        background: #B78A3F;
        
}
.hero-vector {
    display: none;
}

@media only screen and (max-width: 768px) {
    .hero-title {
        width: 100%;
        padding: 0 20px;
        font-size: 32px;
        line-height: 37px;
    }
    .hero-navigation {
        display: none;
    }
    .hero-link {
        display: block;
        text-align: center;
        font-size: 28px;
        line-height: 33px;
        margin: 267px 0 75px;
    }
    .hero-vector {
        display: inline-block;
        margin: 28px 47% 0;
    }
    .hero-partners-item{
        width: 20%;
        margin: 0 22px 232px 0;
    }
    .hero-burger {
        display: inline-block;
        width: 5%;
        margin: 0 22px 232px 20%;
    }
    .hero-burger-show {
        display: inline-block;
        width: 5%;
        margin: 0 22px 232px 20%;
    }

}




.sectors {
    padding: 50px 0;
    
}
.sectors-title {
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    color: #ffffff;
    text-align: center;
    margin-bottom: 44px;
}
.sectors-subtitle {
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;
    color: #ffffff;
    margin: 42px 0 30px;
}
.sectors-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.sectors-list-item {
    display: flex;
    align-items: center;
    position: relative;
    width: calc((100% / 3) - 70px);
    height: 240px;
    margin-bottom: 30px;
}
.sectors-list-item-name {
    font-weight: normal;
    font-size: 23px;
    line-height: 30px;
    color: #222222;
    background: rgba(255, 255, 255, 0.83);
    width: 100%;
    text-align: center;
}
.sectors-list-item-tooltip{
    background-color: #DADADA;
    padding: 16px 16px 16px 75px;
    -webkit-clip-path: polygon(15% 0, 100% 0, 100% 100%, 15% 100%, 0 50%);
            clip-path: polygon(15% 0, 100% 0, 100% 100%, 15% 100%, 0 50%);
    position: absolute;
    left: 100%;
    width: 450px;
    z-index: 1;
    pointer-events: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-animation: fadeIn 0.4s ease-in-out;
            animation: fadeIn 0.4s ease-in-out;

}
.sectors-list-item-tooltip-switched {
    left: auto;
    left: initial;
    right: 100%;
    -webkit-clip-path: polygon(0 0, 85% 0%, 100% 50%, 85% 100%, 0 100%);
            clip-path: polygon(0 0, 85% 0%, 100% 50%, 85% 100%, 0 100%);
    padding: 16px 75px 16px 16px;
}

@-webkit-keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@media only screen and (max-width: 768px) {
    .sectors-title {
        font-weight: normal;
        font-size: 27px;
        line-height: 32px;
        text-align: center;
    }
    .sectors-subtitle {
        display: none;
    }
    .sectors-list-item {
        width: 100%;
        height: 267px;
        margin: 10px;
    }
    .sectors-list-item-tooltip, .sectors-list-item-tooltip-switched {
        left: 0;
        right: 0;
        top: 90%;
        width: 86%;
        -webkit-clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
                clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
    }
}
.facts {
    position: relative;
    box-sizing: border-box;
    padding: 100px 60px 50px;
}
.facts-title {
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    color: #ffffff;
    text-align: center;
    margin-bottom: 30px;
}
.facts-slide-desc {
    font-weight: normal;
    font-size: 27px;
    line-height: 32px;
    text-align: center;
    color: #ffffff;
    margin-top: 40px;
}
.swiper-button-next,.swiper-button-prev {
    width: 40px;
    height: 40px;
    color:#B78A3F;
    ;
}
.swiper-button-next {
    right: 10px;
}
.swiper-button-prev {
    left: 10px;
}
.facts-pagination {
    display: flex;
    justify-content: center;
    margin-top: 66px;
}
.facts-pagination-bullet {
    display: inline-block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: transparent;
    border: 2px solid #B78A3F;
    opacity: 1;
    transition: 0.3s background-color;
    margin: 0 20px;

}
.swiper-pagination-bullet-active {
    background-color: #B78A3F;
}
.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
    opacity: 0;
}
